<template>
  <div class="views" id="views">
    <div id="container" class="container"></div>
  </div>
</template>

<script>
import { App } from '@/assets/App/App'
import { GUI } from 'three/examples/jsm/libs/dat.gui.module'

const app = new App()
export default {
  data () {
    return {
      params: {
        methods: {
          animation: true, // 循环调用渲染
          renderer: true, // 应用渲染器
          light: true, // 在场景中加入全景光
          controls: true, // 使用轨道控制器，鼠标滚轮控制放大缩小，左键拖动绕中心点旋转，右键拖动中心点。
          statement: true // 在控制台打印声明，与本案例无关，可忽略。
        },
        events: {
          windowResize: true // 监听窗口尺寸变化，并改变渲染页面尺寸
        },
        params: {
          container: null, // 渲染结果输出dom页面
          renderer: [
            'WebGLRenderer'
          ],
          controls: [
            'firstPerson'
          ],
          rendererParams: {
            antialias: true // 抗锯齿，可忽略。
          }
        }
      },
      firstPersonParams: {
        enabled: true, // 是否启用
        applyGravity: true, // 是否应用重力
        applyCollision: true, // 是否应用碰撞
        positionEasing: true, // 是否应用抖动减缓
        applyJump: false, // 是否应用跳跃
        lookSpeed: 0.008, // 视角拖动速度（灵敏度）
        moveSpeed: 1, // 移动速度
        playerHeight: 10, // 距地面最小高度
        collisionDistance: 5, // 距墙壁最小距离
        g: 9.8 // 重力系数
      }
    }
  },
  mounted () {
    this.params.params.container = document.getElementById('container') // 获取渲染结果输出dom页面
    /**
     * 初始化场景
     */
    app.init(this.params, (scene, camera, renderer) => {
      app.firstPerson.colliders = scene
    })
    /**
     * 导入模型
     */
    app.addModel({
      url: 'static/model/fbx/cqcet/15/完整实训楼.FBX', // 模型地址
      type: 'fbx' // 模型格式
    }, () => { // 加载完成回调，可忽略。
      console.log('模型加载完成')
    })
    /**
     * 使用gui调试轨道控制器
     */
    var gui = new GUI({ width: 300 })
    this.gui = gui
    gui.add(this.firstPersonParams, 'enabled').onChange(function (value) {
      app.firstPerson.enabled = value
    })
    gui.add(this.firstPersonParams, 'applyGravity').onChange(function (value) {
      app.firstPerson.applyGravity = value
    })
    gui.add(this.firstPersonParams, 'applyCollision').onChange(function (value) {
      app.firstPerson.applyCollision = value
    })
    gui.add(this.firstPersonParams, 'positionEasing').onChange(function (value) {
      app.firstPerson.positionEasing = value
    })
    gui.add(this.firstPersonParams, 'applyJump').onChange(function (value) {
      app.firstPerson.applyJump = value
    })
    gui.add(this.firstPersonParams, 'lookSpeed', 0.00, 1.00).onChange(function (value) {
      app.firstPerson.lookSpeed = Number(value)
    })
    gui.add(this.firstPersonParams, 'moveSpeed', 0, 10).onChange(function (value) {
      app.firstPerson.moveSpeed = Number(value)
    })
    gui.add(this.firstPersonParams, 'playerHeight', 0, 20).onChange(function (value) {
      app.firstPerson.playerHeight = Number(value)
    })
    gui.add(this.firstPersonParams, 'collisionDistance', 0, 20).onChange(function (value) {
      app.firstPerson.collisionDistance = Number(value)
    })
    gui.add(this.firstPersonParams, 'g', 1, 20).onChange(function (value) {
      app.firstPerson.applyJump = Number(value)
    })
  },
  beforeDestroy () {
    this.gui.destroy()
  }
}
</script>

<style scoped>
.views {
  height: 100%;
  width: 100%;
}

.container {
  height: 100%;
  width: 100%;
}
</style>
